<template>
  <div class="fill-height">
    <app-bar title="Cartão de Regularidade">
      <template v-slot:append>
        <v-btn icon to="/" replace>
          <v-icon>mdi-arrow-left</v-icon>
        </v-btn>
      </template>
    </app-bar>
    <async-container
      :loading="!!loading"
      :text="typeof loading === 'string' ? loading : undefined"
      fluid
      class="pa-0"
    >
      <transition mode="out-in" name="flip">
        <pinch-zoom
          v-if="exibirFrente"
          :key="0"
          :class="{'white': !$store.state.dark, 'black': $store.state.dark}"
          style="height: calc(100vh - 56px)"
        >
          <img :src="imgFrente" alt="" style="max-width: 100%">
        </pinch-zoom>
        <pinch-zoom
          v-else
          :key="1"
          :class="{'white': !$store.state.dark, 'black': $store.state.dark}"
          style="height: calc(100vh - 56px)"
        >
          <img :src="imgVerso" alt="" style="max-width: 100%">
        </pinch-zoom>
      </transition>
      <v-btn
        fab
        @click="exibirFrente = !exibirFrente"
        :disabled="!!loading"
        color="success"
        fixed
        bottom
        right
      >
        <v-icon>mdi-axis-z-rotate-counterclockwise</v-icon>
      </v-btn>
    </async-container>
  </div>
</template>

<script>
import AsyncContainer from '@/components/AsyncContainer';
import AppWebClient from '@/http/AppWebClient';
import ImageHelper from '@/helpers/ImageHelper';
import AppBar from '@/components/AppBar';
export default {
  name: 'Carp',
  components: {AppBar, AsyncContainer},
  data: () => ({
    loading: true,
    imgFrente: '', //base64
    imgVerso: '',  //base64
    exibirFrente: true, //alterna frente e verso
    msgErro: '',
  }),
  async created() {
    this.loadData();
  },
  methods: {
    async loadData() {
      this.loading = 'Validando aptidão';
      const appWebClient = new AppWebClient();
      try {
        const frente = await appWebClient.getCarpFrente();
        const verso = await appWebClient.getCarpVerso();
        this.imgFrente = await ImageHelper.blobToDataURI_async(frente);
        this.imgVerso = await ImageHelper.blobToDataURI_async(verso);
        this.loading = false;
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
        await this.$router.push('/');
      }
    }
  },
}

//Para clientes SPW na montagem do cartão:
// if (regional === 1 || regional === 4) {
//   const spwWebClient = new SpwWebClient(regional);
//   const fichaSwp = await spwWebClient.getSituacaoFinanceira(this.$store.getters['session/payload'].cpf);
//   if (fichaSwp.situacaoCadastral !== 'ATIVO') this.msgErro = 'Você não está ativo no conselho';
//   else if (fichaSwp.situacaoFinanceira !== 'EM DIA') this.erro = 'Você não está em dia com o conselho regional';
//   else await spwWebClient.updateCofeciValidadeDocumento(parseInt(this.$store.state.ficha.cofeci['Creci'].substr(9)), this.$store.getters['session/payload'].cpf, fichaSwp);
// }
</script>

<style>
  .flip-enter-active,
  .flip-leave-active {
    transition-duration: 300ms;
    transition-property: all;
    transition-timing-function: ease-out;
  }
  .flip-enter,
  .flip-leave-to {
    transform: rotateY(90deg);
  }
  .flip-leave,
  .flip-enter-to {
    transform: rotateY(0);
  }
</style>